import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { UsersService } from '../../modules/users/users.service';
import { Identity } from 'src/app/shared/models/Identity';
import { Router } from '@angular/router';


@Component({
    selector: 'meedias-redactor-user',
	templateUrl: './user.template.html',
	styleUrls: ['./user.styles.scss']
})
export class UserComponent implements OnInit {

	constructor(
		private userService: UsersService,
		private router:Router
	){}

	@Output() userDeleted = new EventEmitter<string>();
	@Input() loadedUser: Identity;
	user;
	loaded: boolean;

	ngOnInit(){
		this.loaded = false;
		this.userService.getUser(this.loadedUser._id).then((data) => {
            let user =new Identity(this.loadedUser._id,data.nick,data.email,data.display,data.location,data.birthday);
			this.user = user;
			this.loaded = true;
		});
	
	}

	editUser(id){
		let url = 'redactor/users/edit/' + id;
		this.router.navigate([url]);
	}


	deleteUser(id){
		this.loaded = false;
		this.userService.removeUser(id).then(() => {
			this.userDeleted.emit(id);
		})
	}

 
}
